/** Media Queries **/
*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  background-image: url("/build/img/bg-1920.webp");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}
.contenedor h1 {
  background: linear-gradient(to right, #27b66c 0%, #8fe7b9 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 7rem;
}
@media (min-width: 768px) {
  .contenedor h1 {
    font-size: 8rem;
  }
}
.contenedor h1 span {
  background: linear-gradient(to right, white 0%, white 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 7rem;
}
@media (min-width: 768px) {
  .contenedor h1 span {
    font-size: 8rem;
  }
}
.contenedor p {
  color: #fff;
  text-transform: uppercase;
  font-size: 2rem;
  text-align: center;
}
.contenedor .formulario {
  color: #fff;
}
.contenedor .formulario .boton {
  background-color: #27b66c;
  padding: 1.5rem 4rem;
  color: white;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .contenedor .formulario .boton {
    width: auto;
  }
}
.contenedor .formulario .boton:hover {
  background-color: #1e8c53;
  cursor: pointer;
}
.contenedor .formulario a {
  color: #fff;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

footer {
  margin-top: 35.9rem;
  padding: 2rem;
}
footer p {
  background: linear-gradient(to right, #fff 0%, white 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 7rem;
}
@media (min-width: 768px) {
  footer p {
    font-size: 8rem;
  }
}
@media (min-width: 768px) {
  footer p {
    font-size: 2rem;
  }
}
footer p span {
  color: #50da92;
}

