.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #fff;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}
.acciones a:hover {
  text-decoration: underline;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  max-width: 80rem;
}
.alerta.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta.exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

.crear h1 {
  margin-bottom: -1rem;
}
@media (min-width: 768px) {
  .crear .acciones {
    align-items: center;
  }
}

.confirmar .acciones {
  justify-content: center;
}
.confirmar .acciones .boton {
  background-color: #27b66c;
  padding: 1.5rem 4rem;
  color: #fff;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .confirmar .acciones .boton {
    width: auto;
  }
}
.confirmar .acciones .boton:hover {
  background-color: #1e8c53;
  cursor: pointer;
}

.mensaje .alerta {
  padding: 2rem;
  text-align: center;
}
.mensaje .acciones .boton {
  background-color: #27b66c;
  padding: 1.5rem 4rem;
  color: #fff;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .mensaje .acciones .boton {
    width: auto;
  }
}
.mensaje .acciones .boton:hover {
  background-color: #1e8c53;
  cursor: pointer;
}

@media (min-width: 768px) {
  .olvide .acciones {
    align-items: center;
  }
}

.menu .acciones {
  width: 100%;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .menu .acciones {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
}
.menu .acciones h1 {
  background: linear-gradient(to right, #f4f6f7 0%, white 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 7rem;
}
@media (min-width: 768px) {
  .menu .acciones h1 {
    font-size: 8rem;
  }
}
@media (min-width: 768px) {
  .menu .acciones h1 {
    font-size: 4rem;
  }
}
.menu .acciones a {
  border-radius: 0.3rem;
  padding: 1rem 2rem 1rem;
  font-size: 3rem;
  margin-bottom: 0;
  transition-property: background;
  transition-duration: 500ms;
}
.menu .acciones a:hover {
  text-decoration: none;
  background: #27b66c;
}
.menu .contenedor {
  max-width: 95%;
}
.menu .contenedor nav {
  display: flex;
  justify-content: space-around;
  border-bottom: gray solid 0.2rem;
}
.menu .contenedor nav a {
  border-radius: 0.3rem;
  padding: 1rem 2rem 1rem;
  font-size: 2.5rem;
  color: #fff;
  transition-property: background;
  transition-duration: 500ms;
}
.menu .contenedor nav a:hover {
  background: #27b66c;
}
.menu .contenedor .contenedor-sm {
  margin-top: 7rem;
  max-width: inherit;
}
.menu .contenedor .contenedor-sm h1 {
  padding: 1rem;
  border-top: 0.2rem solid rgb(45, 170, 128);
  border-bottom: rgb(45, 170, 128) solid 0.2rem;
  background: linear-gradient(to right, #f4f6f7 0%, white 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 7rem;
}
@media (min-width: 768px) {
  .menu .contenedor .contenedor-sm h1 {
    font-size: 8rem;
  }
}
@media (min-width: 768px) {
  .menu .contenedor .contenedor-sm h1 {
    font-size: 3rem;
  }
}
.menu .contenedor .contenedor-sm .ejercicios {
  width: 80%;
  margin: 0 auto;
  background-color: red;
}